import { useEffect, useState } from "react";
import "./Graph.css";
import GraphDataBox from './GraphDataBox/GraphDataBox'
import { ResponsiveBar } from '@nivo/bar';
import { api } from "../../../model/model";

const Graph = ({ }) => {

    const getOneWeekAgo = () => {
        const oneWeekAgo = new Date();
        oneWeekAgo.setDate(oneWeekAgo.getDate() - 6);
        return oneWeekAgo.toISOString().split('T')[0];
    };

    const [data, setData] = useState([])
    const [date, setDate] = useState({
        enddate: new Date().toISOString().split('T')[0],
        startdate: getOneWeekAgo()
    })

    const changeDate = (event) => {
        setDate(prev => ({
            ...prev,
            [event.target.name]: event.target.value
        }))
    }

    useEffect(() => {
        api(`/visit/select?`, 'post', {
            enddate: new Date(date.enddate),
            startdate: new Date(date.startdate)
        })
            .then(res => {
                setData(res.data)
                console.log(res.data)
            })
            .catch(err => {
                console.log(err.message)
            })
    }, [date])
    const handle = {
        barClick: (data) => {
            console.log(data);
        },

        legendClick: (data) => {
            console.log(data);
        },
    };

    return (
        <div id="GraphContainer">
            <div id="graphHead">
                <h3>
                    <i className="fa-solid fa-list"></i> 
                </h3>
                <div id='optionBar'>
                    <label htmlFor=""> DATA -
                        <select name="" id="">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </label>
                    <label htmlFor="">  -
                        <select name="" id="">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </label>
                    <label htmlFor="">  -
                        <select name="" id="">
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </label>
                    <label htmlFor="">  -
                        <input type="date" name="startdate" value={date.startdate} onChange={changeDate} />
                        ~
                        <input type="date" name="enddate" value={date.enddate} onChange={changeDate} />
                    </label>
                    <div id='optionBTN'></div>
                </div>
            </div>
            <div id="graphBox">
                <div style={{ width: '100%', height: '100%', margin: '0 auto' }}>
                    <ResponsiveBar
                        /**
                         * chart  
                         */
                        data={data.reverse()}
                        /**
                         * chart   key ( )
                         */
                        keys={['homeCount', 'listCount', 'detailCount', 'orderCount']}
                        /**
                         * keys  index key ( )
                         */
                        indexBy="visitDate"
                        /**
                         * chart margin
                         */
                        margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
                        /**
                         * chart padding (bar )
                         */
                        padding={0.3}
                        /**
                         * chart 
                         */
                        colors={['#CA4046', '#F2EECA', '#A0D3F9', '#F6C026']} //   
                        // colors={{ scheme: 'nivo' }} // nivo     
                        /**
                         * color  
                         */
                        colorBy="id" //  keys   
                        // colorBy="indexValue" // indexBy    
                        theme={{
                            /**
                             * label style (bar  )
                             */
                            labels: {
                                text: {
                                    fontSize: 14,
                                    fill: '#000000',
                                },
                            },
                            /**
                             * legend style (default     key )
                             */
                            legends: {
                                text: {
                                    fontSize: 12,
                                    fill: '#000000',
                                },
                            },
                            axis: {
                                /**
                                 * axis legend style (bottom, left  )
                                 */
                                legend: {
                                    text: {
                                        fontSize: 20,
                                        fill: '#000000',
                                    },
                                },
                                /**
                                 * axis ticks style (bottom, left  )
                                 */
                                ticks: {
                                    text: {
                                        fontSize: 16,
                                        fill: '#000000',
                                    },
                                },
                            },
                        }}
                        /**
                         * axis bottom 
                         */
                        axisBottom={{
                            tickSize: 5, //      
                            tickPadding: 5, // tick padding
                            tickRotation: -10, // tick 
                            // legend: '', // bottom 
                            legendPosition: 'middle', //  
                            legendOffset: 40, //  chart 
                        }}
                        /**
                         * axis left 
                         */
                        axisLeft={{
                            tickSize: 0, //      
                            tickPadding: 0, // tick padding
                            tickRotation: 0, // tick 
                            legendPosition: 'middle', //  
                            legendOffset: -60, //  chart 
                        }}
                        /**
                         * label    width
                         */
                        labelSkipWidth={36}
                        /**
                         * label    height
                         */
                        labelSkipHeight={12}
                        /**
                         * bar  
                         */
                        onClick={handle.barClick}
                        /**
                         * legend  (default     key )
                         */
                        legends={[
                            {
                                dataFrom: 'keys', //   
                                anchor: 'bottom-right', // 
                                direction: 'column', // item  
                                justify: false, // ,   justify  
                                translateX: 120, // chart X 
                                translateY: 0, // chart Y 
                                itemsSpacing: 2, // item 
                                itemWidth: 100, // item width
                                itemHeight: 20, // item height
                                itemDirection: 'left-to-right', // item   
                                itemOpacity: 0.85, // item opacity
                                symbolSize: 20, // symbol ( ) 
                                effects: [
                                    {
                                        //    (hover item opacity 1 )
                                        on: 'hover',
                                        style: {
                                            itemOpacity: 1,
                                        },
                                    },
                                ],
                                onClick: handle.legendClick, // legend  
                            },
                        ]}
                    />
                </div>
            </div>
            {data.length > 0 && <GraphDataBox data={data} />}
        </div>
    );
}

export default Graph;